.sidemenu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 3rem;
  min-height: 100%;

  ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding-left: 2rem;
    list-style: none;

    a {
      display: flex;
      align-items: center;
      gap: 1ch;
    }
  }

  &__logo {
    padding: 1rem;
  }
}
